import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

class Draggable extends PureComponent {
	static get propTypes() {
		return {
			axisX             : PropTypes.bool,
			axisY             : PropTypes.bool,
			handle            : PropTypes.string,
			grid              : PropTypes.array,
			onDragStart       : PropTypes.func,
			onDragStop        : PropTypes.func,
			onDrag            : PropTypes.func,
			containment       : PropTypes.PropTypes.oneOf(['parent', 'window']),
			draggingClassName : PropTypes.string,
			children          : PropTypes.node.isRequired,
		};
	}

	static get defaultProps() {
		return {
			axisX             : true,
			axisY             : true,
			handle            : null,
			children          : null,
			containment       : 'window',
			draggingClassName : 'dragging',
			grid              : [1, 1],
			onDragStart       : () => {},
			onDragStop        : () => {},
			onDrag            : () => {}
		}
	}

	get coordinates() {
		return {
			x : this.state.x,
			y : this.state.y
		}
	}

	constructor() {
		super();

		this.elementRef = React.createRef();

		this.state = {
			x        : null,
			y        : null,
			dragging : false
		};

		this.stopDrag  = this.stopDrag.bind(this);
		this.startDrag = this.startDrag.bind(this);
		this.onDrag    = this.onDrag.bind(this);

		this.initialMouseX = null;
		this.initialMouseY = null;
		this.handleElement = null;
	}

	componentDidMount() {
		this.handleElement = this.elementRef.current.querySelector(this.props.handle);

		if(this.handleElement) {
			this.handleElement.addEventListener("mousedown", this.startDrag);
		}

		const rect = this.elementRef.current.getBoundingClientRect();

		this.setState({
			x : rect.x,
			y : rect.y
		});
	}

	componentWillUnmount() {
		document.removeEventListener("mouseup", this.stopDrag);
		document.removeEventListener("mousemove", this.onDrag);

		if(this.handleElement) {
			this.handleElement.removeEventListener("mousedown", this.startDrag);
		}
	}

	snapToGrid({x,y}) {
		const gridX = this.props.grid[0];
		const gridY = this.props.grid[1];

		return {
			x : Math.round(x / gridX) * gridX,
			y : Math.round(y / gridY) * gridY
		}
	}

	getMaxMinCoordinates({x ,y}) {
		const currentEl       = this.elementRef.current;
		const currentElRect   = currentEl.getBoundingClientRect();
		const currentElWidth  = currentElRect.width;
		const currentElHeight = currentElRect.height;

		let containmentElWidth  = null;
		let containmentElHeight = null;

		if(this.props.containment === 'window') {
			containmentElWidth  = window.innerWidth;
			containmentElHeight = window.innerHeight;
		}
		else {
			const parentElRect  = currentEl.parentNode.getBoundingClientRect();
			containmentElWidth  = parentElRect.width;
			containmentElHeight = parentElRect.height;
		}

		return {
			x : Math.max(Math.min(containmentElWidth  - currentElWidth,  x), 0),
			y : Math.max(Math.min(containmentElHeight - currentElHeight, y), 0)
		}
	}

	startDrag(event) {
		if(this.handleElement && event.currentTarget !== this.handleElement) {
			return;
		}

		this.setState({
			dragging : true
		});

		this.initialMouseX = event.pageX - this.state.x;
		this.initialMouseY = event.pageY - this.state.y;

		this.props.onDragStart(event, this.coordinates);

		document.addEventListener("mouseup", this.stopDrag);
		document.addEventListener("mousemove", this.onDrag);
	}

	stopDrag(event) {
		this.setState({
			dragging : false
		});

		this.initialMouseX = null;
		this.initialMouseY = null;

		this.props.onDragStop(event, this.coordinates);

		document.removeEventListener("mouseup", this.stopDrag);
		document.removeEventListener("mousemove", this.onDrag);
	}

	onDrag(event) {
		if(!this.state.dragging) {
			return;
		}

		let x = event.pageX - this.initialMouseX;
		let y = event.pageY - this.initialMouseY;

		x = this.props.axisX ? x : this.state.x;
		y = this.props.axisY ? y : this.state.y;

		let coordinates = {x, y};
		coordinates = this.snapToGrid(coordinates);
		coordinates = this.getMaxMinCoordinates(coordinates);

		this.setState(coordinates);
		this.props.onDrag(event, coordinates);
	}

	render() {
		return React.cloneElement(React.Children.only(this.props.children), {
			ref         : this.elementRef,
			onMouseDown : this.startDrag,
			className   : classNames(this.props.children.props.className, {
				[this.props.draggingClassName] : this.state.dragging
			}),
			style       : {
				position : 'fixed',
				left     : this.state.x + 'px',
				top      : this.state.y + 'px',
				bottom   : this.state.y !== null ? 'auto' : null,
				right    : this.state.x !== null ? 'auto' : null
			}
		});
	}
}

export default Draggable;
